

































import { Vue, Component } from "vue-property-decorator";

@Component
export default class AppHeaderNav extends Vue {
	showNav: boolean = false;

	setShowNav(show: boolean) {
		this.showNav = show;
	}
	toggleShowNav() {
		this.showNav = !this.showNav;
	}

	get classes() {
		return {
			"app-header-nav": true,
			"show": this.showNav
		};
	}
}
