






































































import { Vue, Component, Prop } from "vue-property-decorator";
// Components //
import AppIcon from "@/components/app/icon.vue";

@Component({
	components: {
		AppIcon
	}
})
export default class AppTeamsTeamBlock extends Vue {
	@Prop({
		type: String,
		required: true
	}) img!: string;
	@Prop({
		type: String,
		required: true
	}) title!: string;
	@Prop({
		type: String,
		required: true
	}) name!: string;
	@Prop({
		type: String,
		required: true
	}) description!: string;
	@Prop({
		type: Array,
		required: true
	}) materials!: string[];
	@Prop({
		type: Array,
		required: true
	}) classes!: string[];

	isFocus: boolean = false;
	isFocusClose: boolean = false;
	showPopup: boolean = false;

	changeShowPopup(show: boolean) {
		this.showPopup = show;
		document.body.style.overflowY = show ? "hidden" : "auto";
	}

	onFocus() {
		this.isFocus = true;
	}
	onBlur() {
		this.isFocus = false;
	}
	onFocusClose() {
		this.isFocusClose = true;
	}
	onBlurClose() {
		this.isFocusClose = false;
	}

	onEnter() {
		if (this.isFocusClose) this.changeShowPopup(false);
		else if (this.isFocus) this.changeShowPopup(true);
	}
	onEsc() {
		if (this.showPopup) this.changeShowPopup(false);
	}

	get thumbnailUrl() {
		return require(`@/assets/teams/front/${this.img}.png`);
	}
	get backUrl() {
		return require(`@/assets/teams/back/${this.img}.png`);
	}

	get cardClasses() {
		return {
			"card": true,
			"selected": this.showPopup
		};
	}
	get popupClasses() {
		return {
			"popup": true,
			"show": this.showPopup
		};
	}
	get frontStyle() {
		return {
			"background-image": `url(${this.thumbnailUrl})`
		};
	}
	get backStyle() {
		return {
			"background-image": `url(${this.backUrl})`
		};
	}
}
